import { selectAll } from 'lib/selectors'

/**
 * Handles a collection of collapse modules to keep only one open
 * @constructor
 * @param {Node} el - The node element that contains of the collapse modules
 */

function Accordion(el) {
  const collapseEls = selectAll('[data-module=collapse]', el)
  collapseEls.forEach(collapseEl => collapseEl.addEventListener('COLLAPSE_CHANGE', handleCollapse))

  function handleCollapse(e) {
    if (!e.detail.isVisible) {
      return
    }
    collapseEls.forEach(collapseEl => {
      if (collapseEl !== e.currentTarget) {
        const collapse = collapseEl.data('collapse')
        collapse.hide()
      }
    })
  }
}

export default Accordion
